//modulos
import React, { Component } from "react";
import { Redirect } from "react-router";
import { Alert } from "react-bootstrap";
//assets
import "./login.css";

/**
 * @author @AmelieCruz
 * Componente que define la vista del Login
 * @argument {boolean} isLogged -- estado que indica si el usuario est√° loggeado
 * @argument {Socket} socket -- websocket conectado al server
 * @argument {Function} setUserInfo -- handler de informaci√≥n del usuario para cuando se loggea
 * @argument {Function} setIsLogged -- handler del estado isLogged para cuando se loggea
 */
export default class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            usuario: "",
            password: "",
			sending: false,
			colorAlert: "success",
			showAlert: false,
			alertMessage: ""
        }
        this.handleChangeUser = this.handleChangeUser.bind(this);
        this.handleChangePassword = this.handleChangePassword.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.setSocketListeners = this.setSocketListeners.bind(this);
        this.handleEnter = this.handleEnter.bind(this);
        this.input = undefined;
    }
    componentDidMount() {
        this.setSocketListeners()
    }
    setSocketListeners() {
        const socket = this.props.socket;
        socket.on("error-login", errorCount => {
            this.setState({
				alertMessage: "Ocurri√≥ un error al iniciar sesi√≥n",
				colorAlert: "danger",
				showAlert: true
			})
        })
        socket.on("incorrect-login", errorCount => {
            this.setState({
				alertMessage: "El usuario o la contrase√±a son incorrectos",
				colorAlert: "danger",
				showAlert: true
			})
        })
        socket.on("logged", userInfo => {
            this.props.setUserInfo(userInfo);
            this.props.setIsLogged(true);
        })
    }
    handleChangePassword(event) {
        const {target } = event
        this.setState({ password: target.value });
        if(target.value.includes("--")){
            target.setCustomValidity("La contrase√±a no puede contener la cadena especial '--'")
            target.reportValidity();
            target.valid = false;
        }else{
            target.valid = true;
        }
    }
    handleChangeUser(event) {
        const {target} = event
        this.setState({ usuario: target.value });
        if(target.value.includes("--")){
            target.setCustomValidity("El usuario debe ser un correo v√°lido, no puede contener la cadena especial '--'")
            target.reportValidity();
            target.valid = false;
        }else{
            target.valid = true;
        }
    }
    handleEnter(event = Event) {
        // console.log("se teclea: ", event, " ", event);
        if (event.wich === 13 || event.keyCode === 13) {
            this.handleSubmit();
        }
    }

    handleSubmit() {
        const socket = this.props.socket;
        if (this.state.password == "" || this.state.usuario == "") {
            alert("debes rellenar todos los campos");
            return;
        }
        const usr = document.getElementById("inputUsuario")
        const pass = document.getElementById("inputContrase√±a")
        if(!usr.valid || !pass.valid ) {
            console.log("not valid");
            usr.reportValidity();
            pass.reportValidity();
            return
        }
        this.setState({
            sending: true
        })
        socket.emit("login", {
            userName: this.state.usuario,
            password: this.state.password
        });
    }
    render() {

        console.log(this.input);
        if (this.props.isLogged) {
            return <Redirect to='/' />
        } else
            return (
                <div className="hvh-100 d-flex justify-content-start flex-column">
                    <div className="alert-container">
						<div className="alert-size">
							{this.state.showAlert ?
								<Alert
									variant={this.state.colorAlert}
									onClose={() => this.setState({ showAlert: false })}
									dismissible
								>
									<Alert.Heading
										as="h5"
										className="text-center"
									>
										{this.state.alertMessage?.toUpperCase()}
									</Alert.Heading>
								</Alert>
								:
								""
							}
						</div>
					</div>
                    <h1 className="text-center display-6 m-5">Iniciar Sesi√≥n</h1>
                    <div className="mt-5 d-flex justify-content-center align-itens-center h-100" >
                        <form className="login-form">
                            <div className="form-floating mb-4">
                                <input
                                    type="email"
                                    className="form-control"
                                    id="inputUsuario"
                                    onKeyDown={this.handleEnter}
                                    placeholder="introduce tu nombre de usuario"
                                    onChange={this.handleChangeUser}

                                />
                                <label htmlFor="inputUsuario" >Usuario</label>
                            </div>
                            <div className="form-floating">
                                <input
                                    type="password"
                                    className="form-control"
                                    id="inputContrase√±a"
                                    placeholder="introduce tu nombre de usuario"
                                    onKeyDown={this.handleEnter}
                                    onChange={this.handleChangePassword} />
                                <label htmlFor="inputContrase√±a" >Contrase√±a</label>
                            </div>
                            <div className="form-group d-flex justify-content-center mt-5">
                                <a className={`btn btn-primary form-control ${this.state.sending?"disabled":""} `} role="button" onClick={this.handleSubmit}>Iniciar Sesi√≥n</a>
                            </div>
                        </form>
                    </div>
                </div>
            )
    }
}